import React from "react";
import styled from "styled-components";

import img1 from "../assets/Images/1.webp";
import img3 from "../assets/Images/2.webp";

const Section = styled.section`
  position: relative;
  min-height: 100vh;
  width: 80vw;
  display: flex;

  margin: 0 auto;

  @media (max-width: 48em) {
    width: 90vw;
  }
  @media (max-width: 30em) {
    width: 100vw;
  }
`;
const Title = styled.h1`
  font-size: ${(props) => props.theme.fontBig};
  font-family: "Kaushan Script";
  font-weight: 300;
  color: ${(props) => props.theme.textColor || '#757780'};
  position: absolute;
  top: 1rem;
  left: 5%;
  z-index: 5;

  @media (max-width: 64em) {
    font-size: ${(props) => `calc(${props.theme.fontBig} - 5vw)`};
    top: 0;
    left: 0;
  }
  @media (max-width: 48em) {
    font-size: ${(props) => props.theme.fontxxxl};
  }
`;
const Left = styled.div`
  width: 50%;
  font-size: ${(props) => props.theme.fontmd};
  font-family: "adelphi-pe-variable", serif;
  font-weight: 600;
  font-style: normal;
  position: relative;
  z-index: 5;
  margin-top: 20%;

  @media (max-width: 64em) {
    width: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    margin: 0 auto;

    padding: 2rem;
    font-weight: 600;

    backdrop-filter: blur(2px);
    background-color: ${(props) => `rgba(${props.theme.textRgba},0.4)`};
    border-radius: 20px;
  }

  @media (max-width: 48em) {
    font-size: ${(props) => props.theme.fontmd};
  }
  @media (max-width: 30em) {
    font-size: ${(props) => props.theme.fontsm};
    width: 70%;
  }
`;
const Right = styled.div`
  width: 50%;
  position: relative;
  img {
    width: 100%;
    height: auto;
  }

  .small-img-1 {
    width: 40%;
    position: absolute;
    right: 85%;
    bottom: 10%;
  }
  .small-img-2 {
    width: 40%;
    position: absolute;
    left: 80%;
    bottom: 30%;
  }

  @media (max-width: 64em) {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 100vh;
      object-fit: cover;
    }
    .small-img-1 {
      width: 30%;
      height: auto;
      left: 5%;
      bottom: 10%;
    }
    .small-img-2 {
      width: 30%;
      height: auto;
      left: 60%;
      bottom: 20%;
    }
  }
`;

const About = () => {
  return (
    <Section id="fixed-target" className="about">
      <Title
        data-scroll
        data-scroll-speed="-2"
        data-scroll-direction="horizontal"
      >
        About Us
      </Title>
      <Left data-scroll data-scroll-sticky data-scroll-target="#fixed-target">
      WEAR COOL SHIT. Thats our mantra at Project Teamwear. We believe that great team wear does more than match colorsits a powerful statement that brings teams together and makes them look and feel unstoppable. Our portfolio combines style, quality, and affordability so that every team, from local leagues to national competitors, can proudly sport gear that stands out without breaking the bank.
        <br />
        <br />
        To bring each design to life, we rely on our in-house studio, Jersey Kraft. This is where the magic of custom work happens. Jersey Kraft takes your vision and transforms it into high-quality, custom pieces that truly capture your teams identity. From unique colors and logos to innovative styles, Jersey Kraft ensures that every detail is just right, delivering gear that feels personal and powerful.
        <br />
        <br />
        From beer leagues to national competitions, no project is too big or too small for us. Were here to make sure every team has access to gear that stands out and feels authentic, so whether its a single jersey or a full team line-up, Project Teamwear and Jersey Kraft have you covered.
      </Left>
      <Right>
        <img src={img1} alt="About Us" />
        
        <img
          data-scroll
          data-scroll-speed="-2"
          src={img3}
          alt="About Us"
          className="small-img-2"
        />
      </Right>
    </Section>
  );
};

export default About;
